<template>
  <el-container>
    <!-- <div v-wechat-title="$route.meta.title">门诊缴费</div> -->
    <el-header style="height: 42px;">
      <div class="grid-content fonttop">
        <span>就诊人：</span>
        <span>{{oname}}</span>
        <span style="margin:10px">0005674</span>
        <span style="float: right;line-height: 15px;">
          <el-button class="cut">切换</el-button>
        </span>
      </div>
    </el-header>
    <!--  -->
    <el-main>
      <div class="title">
        <div class="title-img"></div>
        <div class="title-font">
          <span>待缴费单</span>
        </div>
      </div>
      <!--  -->
      <el-card class="box-card" v-for="(item,index) in Charge" :key="item.id">
        <div>
          <el-row>
            <el-col :span="8" class="fontr">
              <div class="grid-content bg-purple">单据号</div>
              <div class="grid-content bg-purple">开单科室</div>
              <div class="grid-content bg-purple">开单人</div>
              <div class="grid-content bg-purple">开单日期</div>
            </el-col>
            <el-col :span="16" class="fontc">
              <div>
                <div class="grid-content bg-purple-light">
                  <span>{{item.SerialNumber}}</span>
                </div>
                <div class="grid-content bg-purple-light">
                  <span>{{item.Dept}}</span>
                </div>
                <div class="grid-content bg-purple-light">
                  <span>{{item.DocName}}</span>
                </div>
                <div class="grid-content bg-purple-light">
                  <span>{{item.ChargeDate}}</span>
                </div>
              </div>
            </el-col>
          </el-row>
          <el-divider style="height:.5px"></el-divider>
          <el-row>
            <el-col :span="6">
              <div class="grid-content bg-purple">总金额:</div>
            </el-col>
            <el-col :span="12">
              <div class="grid-content bg-purple-light">
                <span style="color:#FF7800;font-size: 17px;">￥186.00</span>
              </div>
            </el-col>
            <el-col :span="6">
              <div class="grid-content bg-purple-light">
                <router-link tag="a" :to="{ name:'OutpatientTopay',params:{id:index,name:oname}}">
                  <el-button type="primary" class="pay">去缴费</el-button>
                </router-link>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
      <!--  -->
    </el-main>
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';
</style>
<script>
import axios from "axios";
export default {
  name: "Ment",
  data() {
    return {
      showCharge: "",
      Charge: [],
      oname:"黄伟"
    };
  },
  //mounted直接调用
  mounted() {
    
    axios
      .get("http://lc-7fHWOBLD.cn-n1.lcfile.com/7a003906730f9b104c50/json.json")
      .then(response => {
        let rea = response.data.data;
        console.log(rea);
        // forEach加if可以改变原对象.引用类型 -> 类似对象数组
        rea.forEach(e => {
          if (true) {
            e.ChargeDate = new Date(e.ChargeDate)
              .toJSON()
              .replace(/T/g, " ")
              .replace(/\.[\d]{3}Z/, "");
          }
        });
        console.log(rea);
        this.Charge = rea;

        // var tmdate = response.data.data[1].ChargeDate;
        // // 时间格式转换
        // var date2 = new Date(tmdate)
        //   .toJSON()
        //   .replace(/T/g, " ")
        //   .replace(/\.[\d]{3}Z/, "");
        // console.log(date2);
        // // 拷贝接收的rea值 赋值修改后的时间
        // var copy4 = JSON.parse(JSON.stringify(rea));
        // copy4.ChargeDate = date2;
        // console.log(copy4);
        // // 赋值
        // this.Charge = copy4;
      })
      .catch(error => {
        console.log("失败信息");
      });
    //
  }
  //
};
</script>




